import { Dispatch, SetStateAction } from 'react'

import { LatLngLiteral } from '@/types/location'

import { SearchFilter } from './SearchFilter'
import { AirLineWrapper, Header, TitleLocation, TitlePoint, Wrapper, WrapperTitle } from './styles'

type Props = {
  isActive: boolean
  setIsActive: Dispatch<SetStateAction<boolean>>
  pointClick: LatLngLiteral | undefined
  points: LatLngLiteral[]
  setPointActive: Dispatch<SetStateAction<LatLngLiteral | undefined>>
  pointActive: LatLngLiteral | undefined
}

export const AirLine = ({ isActive, setIsActive, points, pointClick, setPointActive, pointActive }: Props) => {
  return (
    <AirLineWrapper isActive={isActive}>
      <SearchFilter setIsActive={setIsActive} />
      <Wrapper>
        <Header>Point Polyline</Header>
        {points.map((point, index) => (
          <WrapperTitle
            active={pointActive?.lat === point.lat && pointActive?.lng === point.lng}
            onClick={() => setPointActive(point)}
            key={index}
          >
            <TitlePoint>{`Point ${index + 1}`}</TitlePoint>
            <div>
              <TitleLocation>{`Lat: ${point.lat}`}</TitleLocation>
              <TitleLocation>{`Lng: ${point.lng}`}</TitleLocation>
            </div>
          </WrapperTitle>
        ))}
        <Header>Point Maker</Header>
        {pointClick && (
          <WrapperTitle
            active={pointActive?.lat === pointClick.lat && pointActive?.lng === pointClick.lng}
            onClick={() => setPointActive(pointClick)}
          >
            <TitlePoint>{`Point 1`}</TitlePoint>
            <div>
              <TitleLocation>{`Lat: ${pointClick.lat}`}</TitleLocation>
              <TitleLocation>{`Lng: ${pointClick.lng}`}</TitleLocation>
            </div>
          </WrapperTitle>
        )}
      </Wrapper>
    </AirLineWrapper>
  )
}

AirLine.displayName = 'AirLine'
