import React, { useCallback, useEffect, useState } from 'react'

import { WrapperClock } from './styles'

const Clock = () => {
  const [time, setTime] = useState(new Date())

  const updateTime = useCallback(() => {
    setTime(new Date())
    requestAnimationFrame(updateTime)
  }, [])

  useEffect(() => {
    updateTime()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const hours = time.getHours().toString().padStart(2, '0')
  const minutes = time.getMinutes().toString().padStart(2, '0')
  const seconds = time.getSeconds().toString().padStart(2, '0')
  const day = time.getDate().toString().padStart(2, '0')
  const dayOfWeek = ['日', '月', '火', '水', '木', '金', '土'][time.getDay()]
  const month = (time.getMonth() + 1).toString().padStart(2, '0')
  const year = time.getFullYear().toString().padStart(2, '0')
  const stringDate = `${year}年${month}月${day}日（${dayOfWeek}）${hours}:${minutes}:${seconds}`

  return <WrapperClock>{stringDate}</WrapperClock>
}

Clock.displayName = 'Clock'
export default Clock
