import { Dispatch, FC, SetStateAction } from 'react'

import IconBack from './assets/back.svg'
import { Icon, WrapperIconBack } from './styles'

type Props = {
  setIsActive: Dispatch<SetStateAction<boolean>>
}

export const SearchFilter: FC<Props> = ({ setIsActive }) => {
  return (
    <WrapperIconBack>
      <Icon onClick={() => setIsActive(false)} src={IconBack} />
    </WrapperIconBack>
  )
}
SearchFilter.displayName = 'SearchFilter'
