import { LoadScript } from '@react-google-maps/api'
import { useState } from 'react'

import { AirLine } from '@/components/pages/Home/AirLine'
import Clock from '@/components/pages/Home/Clock'
import config from '@/configs/env/dev'
import { mapOptions } from '@/constants/positions'
import { LatLngLiteral } from '@/types/location'

import IconMenu from './assets/menu.svg'
import Map from './Map'
import { ButtonToggle, HeaderWrapper, HomeWrapper, Icon, TitleHeader, TitleResult, Wrapper } from './styles'

export const HomeLayout = () => {
  const [isActive, setIsActive] = useState(true)
  const [points, setPoints] = useState<LatLngLiteral[]>([])
  const [pointClick, setPointClick] = useState<LatLngLiteral>()
  const [pointActive, setPointActive] = useState<LatLngLiteral>()

  return (
    <Wrapper>
      <HeaderWrapper>
        <TitleHeader>Google Map Drawing App </TitleHeader>
        <Clock />
      </HeaderWrapper>
      <HomeWrapper>
        <ButtonToggle isActive={isActive} onClick={() => setIsActive((pre) => !pre)}>
          <Icon src={IconMenu} />
          <TitleResult>Menu Item</TitleResult>
        </ButtonToggle>
        <AirLine
          isActive={isActive}
          setIsActive={setIsActive}
          pointClick={pointClick}
          points={points}
          setPointActive={setPointActive}
          pointActive={pointActive}
        />
        <LoadScript googleMapsApiKey={config.keyGoogle.key} language="ja">
          <Map
            mapOptionFlight={mapOptions}
            points={points}
            setPoints={setPoints}
            pointClick={pointClick}
            setPointClick={setPointClick}
            pointActive={pointActive}
            setPointActive={setPointActive}
          />
        </LoadScript>
      </HomeWrapper>
    </Wrapper>
  )
}

HomeLayout.displayName = 'HomeLayout'
